export default {
	name: "tuiSticky",
	props: {
		scrollTop: {
			type: Number
		},
		//吸顶容器距离顶部距离 px
		stickyTop: {
			type: [Number, String]

				,
			default: 0





		},
		//吸顶容器 高度 rpx
		stickyHeight: {
			type: String,
			default: "auto"
		},
		//占位容器背景颜色
		bgColor: {
			type: String,
			default: "none"
		},
		//列表中的索引值
		index: {
			type: [Number, String],
			default: 0
		}
	},
	watch: {
		scrollTop(newValue, oldValue) {
			this.updateStickyChange();
		}
	},

	mounted() {
		this.updateScrollChange();
	},

	onReady() {
		this.updateScrollChange();
	},
	data() {
		return {
			timer: null,
			top: 0,
			height: 0,
			isFixed: false
		};
	},
	methods: {
		updateStickyChange() {
			// debugger
			const top = this.top;
			const height = this.height;
			// 滚动的距离
			const scrollTop = this.scrollTop
			// 吸顶容器距离顶部的距离
			let stickyTop = this.stickyTop

			stickyTop = stickyTop - 60
			stickyTop = stickyTop < 0 ? 0 : stickyTop

			this.isFixed = (scrollTop + stickyTop >= top && scrollTop + stickyTop < top + height) ? true : false
		},
		updateScrollChange() {
			if (this.timer) {
				clearTimeout(this.timer)
				this.timer = null
			}
			this.timer = setTimeout(() => {
				const className = '.tui-sticky-class';
				const query = uni.createSelectorQuery().in(this);
				query.select(className).fields({
					      dataset: true,
					      size: true,
				},function(res){
					// debugger
					console.log("----------", res)
				})
				query.select(className).boundingClientRect((res) => {
					if (res) {
						this.top = res.top + (this.scrollTop || 0);
						this.height = res.height
						this.$emit("change", {
							index: Number(this.index),
							top: this.top
						})
					}
				}).exec()
			}, 0)
		}
	}
}
